<template>
  <v-container>
    <v-row dense>
      <v-col>
        <v-expansion-panels accordion hover tile v-model="showOptions">
          <v-expansion-panel>
            <v-expansion-panel-header>Options</v-expansion-panel-header>
            <v-expansion-panel-content>
              <div class="header">Show additional info:</div>
              <v-checkbox dense v-model="showItems" label="Quest items"></v-checkbox>
              <v-checkbox
                class="indent"
                :disabled="!showItems"
                dense
                v-model="showItemsWhenDone"
                label="when done"
              ></v-checkbox>
              <v-checkbox dense v-model="showObjectives" label="Quest objectives"></v-checkbox>
              <v-checkbox
                class="indent"
                :disabled="!showObjectives"
                dense
                v-model="showObjectivesWhenDone"
                label="when done"
              ></v-checkbox>
              <v-text-field :clearable="true" label="Highlight by name" v-model="search"></v-text-field>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-dialog v-model="dialog" max-width="500" persistent v-if="dialogContent" scrollable>
          <v-card style="height:400px;" v-if="!dialogContent.selected">
            <v-card-title>{{dialogContent.item.name}}</v-card-title>
            <v-card-subtitle>Remove the following items from stock?</v-card-subtitle>
            <v-divider></v-divider>
            <v-card-text>
              <!-- <h3>The following tasks will be marked as done:</h3>
              <div>
                <ul>
                  <li>"{{dialogContent.item.name}}"</li>
                  <li v-for="c in dialogContent.item.children" :key="c.id">"{{c.name}}"</li>
                </ul>
              </div>
              <br />-->
              <v-row v-for="(value, item) in dialogContent.requiredItems" :key="item.id">
                <ItemBox
                  v-for="i in value.raid&&value.raid.amount?value.raid.amount:0"
                  :item="item"
                  :value="value"
                  :raid="true"
                  :checked="$store.state.stocked[value.item.id]?i<=$store.state.stocked[value.item.id].raid:false"
                  :key="item.id+'r'+i"
                  :quests="value.raid.quests"
                  :hideout="value.raid.hideout"
                  @itemToggled="itemToggled"
                />
                <ItemBox
                  v-for="i in value.nonraid&&value.nonraid.amount?value.nonraid.amount:0"
                  :item="item"
                  :value="value"
                  :raid="false"
                  :checked="$store.state.stocked[value.item.id]?i<=$store.state.stocked[value.item.id].nonraid:false"
                  :key="item.id+'n'+i"
                  :quests="value.nonraid.quests"
                  :hideout="value.nonraid.hideout"
                  @itemToggled="itemToggled"
                />
              </v-row>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogContent.yes(true)">Yes</v-btn>
              <v-btn color="blue darken-1" text @click="dialogContent.yes(false)">No</v-btn>
              <v-btn color="red darken-1" text @click="dialogContent.cancle">Cancel</v-btn>
            </v-card-actions>
          </v-card>
          <v-card style="height: 300px;" v-else>
            <v-card-title class="headline">{{dialogContent.item.name}}</v-card-title>
            <v-card-text>
              <v-card-subtitle>Undo progress?</v-card-subtitle>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogContent.yes">Ok</v-btn>
              <v-btn color="red darken-1" text @click="dialogContent.cancle">Cancel</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-treeview
          v-model="doneQuests"
          class="trp-light"
          selected-color="#9a8866"
          dense
          :items="quests"
          selection-type="independent"
          open-all
          hoverable
          transition
          selectable
        >
          <template slot="label" slot-scope="{ item, selected }">
            <v-card outlined>
              <h4
                :class="{'primary--text': search&&item.name.toLowerCase().includes(search.toLowerCase())}"
              >{{item.name}} ({{item.from}})</h4>
              <ul v-if="(!selected || showObjectivesWhenDone) && showObjectives" class="tiny">
                <li v-for="objective in item.objectives" :key="objective.id">{{objective.name}}</li>
              </ul>
              <template dense v-if="(!selected || showItemsWhenDone) && showItems">
                <div
                  v-for="(value, e) in staticData.getRequiredItemsForQuests([item.id])"
                  :key="e.id"
                >
                  <ItemBox
                    v-for="i in value.raid&&value.raid.amount?value.raid.amount:0"
                    :item="e"
                    :value="value"
                    :raid="true"
                    :checked="$store.state.stocked[value.item.id]?i<=$store.state.stocked[value.item.id].raid:false"
                    :key="item.id+'r'+i"
                    :quests="value.raid.quests"
                    :hideout="value.raid.hideout"
                    @itemToggled="itemToggled"
                  />
                  <ItemBox
                    v-for="i in value.nonraid&&value.nonraid.amount?value.nonraid.amount:0"
                    :item="e"
                    :value="value"
                    :raid="false"
                    :checked="$store.state.stocked[value.item.id]?i<=$store.state.stocked[value.item.id].nonraid:false"
                    :key="e.id+'n'+i"
                    :quests="value.nonraid.quests"
                    :hideout="value.nonraid.hideout"
                    @itemToggled="itemToggled"
                  />
                </div>
              </template>
            </v-card>
          </template>
        </v-treeview>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import ItemBox from "./ItemBox.vue";

export default {
  name: "Quests",
  components: { ItemBox },
  data: () => ({
    showOptions: 0,
    quests: [],
    doneQuests: [],
    dialog: false,
    dialogContent: null,
    showItems: false,
    showItemsWhenDone: false,
    showObjectives: false,
    showObjectivesWhenDone: false,
    stocked: {},
    ignore: false,
    search: ""
  }),
  computed: {
    storeDoneQuests: function() {
      console.log("computed storeDoneQuests", this.$store.state.doneQuests);
      return this.$store.state.doneQuests;
    }
  },
  watch: {
    storeDoneQuests: function(newVal) {
      console.log("watch storeDoneQuests", newVal);
      this.ignore = true;
      this.doneQuests = newVal;
      this.ignore = false;
    },
    doneQuests: function(oldVal, newVal) {
      var added = newVal.filter(e => oldVal.indexOf(e) === -1);
      var removed = oldVal.filter(e => newVal.indexOf(e) === -1);
      if (!this.dialog && !this.ignore) {
        if (added && added.length > 0)
          this.toggle(this.staticData.getQuest(added[0]), true);
        else if (removed && removed.length > 0)
          this.toggle(this.staticData.getQuest(removed[0]), false);
      }
    },
    stocked: {
      handler(newVal) {
        this.$store.commit("setStocked", newVal);
      },
      deep: true
    },
    showOptions: function(newVal) {
      this.$store.commit("setShowOptionsQuests", newVal);
    }
  },
  created: function() {
    this.ignore = true;
    this.quests = this.staticData.getStarterQuests();

    this.doneQuests = this.$store.state.doneQuests;
    this.stocked = this.$store.state.stocked;
    this.showOptions = this.$store.state.showOptionsQuests;
  },
  mounted: function() {
    var vm = this;
    this.$nextTick().then(function() {
      vm.ignore = false;
    });
  },
  methods: {
    itemToggled: function(value) {
      if (value.item) {
        if (!this.stocked[value.item]) {
          this.$set(this.stocked, value.item, {
            raid: 0,
            nonraid: 0
          });
        }
        if (value.raid) {
          this.$set(
            this.stocked[value.item],
            "raid",
            this.stocked[value.item].raid + (value.checked ? -1 : 1)
          );
        } else {
          this.$set(
            this.stocked[value.item],
            "nonraid",
            this.stocked[value.item].nonraid + (value.checked ? -1 : 1)
          );
        }
      }
    },
    toggle: function(item, selected) {
      var vm = this;
      var requiredItems = this.staticData.getRequiredItemsForQuests([item.id]);
      this.dialogContent = {
        vm: this,
        selected,
        item,
        requiredItems,
        cancle: function() {
          if (!selected) {
            var index = vm.doneQuests.indexOf(item.id);
            if (index !== -1) {
              vm.doneQuests.splice(index, 1);
            }
          } else {
            vm.doneQuests.push(item.id);
          }
          vm.dialog = false;
        }
      };
      if (selected) {
        this.dialogContent.yes = function() {
          vm.dialog = false;
          var index = vm.doneQuests.indexOf(item.id);
          if (index !== -1) {
            vm.doneQuests.splice(index, 1);
          }
          vm.$store.commit("setDoneQuests", vm.doneQuests);
        };
      } else {
        this.dialogContent.yes = function(updateStock) {
          if (updateStock) {
            vm.$store.commit("removeItemsFromStock", requiredItems);
          }
          vm.dialog = false;
          if (vm.doneQuests.indexOf(item.id) === -1) {
            vm.doneQuests.push(item.id);
          }
          vm.$store.commit("setDoneQuests", vm.doneQuests);
        };
      }
      if (
        Object.keys(vm.$store.state.stocked).filter(key =>
          vm.$store.state.stocked[key].raid
            ? vm.$store.state.stocked[key].raid
            : 0 + vm.$store.state.stocked[key].nonraid
            ? vm.$store.state.stocked[key].nonraid
            : 0
        ).length == 0 ||
        Object.keys(requiredItems).length == 0
      ) {
        this.dialogContent.yes();
      } else {
        this.dialog = true;
      }
    }
  }
};
</script>
<style>
.indent {
  padding-left: 10px !important;
}
.header {
  font-weight: bold;
}
.tiny {
  font-size: 0.7em;
}

