  content: "\a0";
  display: block;
  padding: 2px 0;
  line-height: 1px;
}
.v-treeview-node__label p {
  padding: 0 10px;
}
.v-input--selection-controls {
  margin-top: 0px;
  padding-top: 0px;
}
</style>